@import url('function.less');
@import url('animate.less');

html,body {font-size: 0.28rem;-webkit-tap-highlight-color: rgba(0,0,0,0);}
@orange:#ff6600;
@pink:#f83e7e;

.radial(){
    background:-ms-radial-gradient(ellipse closest-corner at 51% 51%, rgba(253,99,134,1) 0.00%, rgba(245,80,117,1) 64.69% );
    background:-webkit-radial-gradient(ellipse closest-corner at 51% 51%, rgba(253,99,134,1) 0.00%, rgba(245,80,117,1) 64.69% );
    background:-moz-radial-gradient(ellipse closest-corner at 51% 51%, rgba(253,99,134,1) 0.00%, rgba(245,80,117,1) 64.69% );
    background:-o-radial-gradient(ellipse closest-corner at 51% 51%, rgba(253,99,134,1) 0.00%, rgba(245,80,117,1) 64.69% );
    background:radial-gradient(ellipse closest-corner at 51% 51%, rgba(253,99,134,1) 0.00%, rgba(245,80,117,1) 64.69% );
}
.linear(){
    background:-ms-linear-gradient(180deg, rgba(246,66,107,1) 0.00%, rgba(233,27,80,1) 64.69% );
    background:-webkit-linear-gradient(180deg, rgba(246,66,107,1) 0.00%, rgba(233,27,80,1) 64.69% );
    background:-moz-linear-gradient(180deg, rgba(246,66,107,1) 0.00%, rgba(233,27,80,1) 64.69% );
    background:-o-linear-gradient(180deg, rgba(246,66,107,1) 0.00%, rgba(233,27,80,1) 64.69% );
    background:linear-gradient(180deg, rgba(246,66,107,1) 0.00%, rgba(233,27,80,1) 64.69% );
}


.btn {.radial; color: #fff; .txt-ver(0.86rem); .tc; border-radius: 0.43rem; .tc; width: 3.5rem; box-sizing: border-box;.d-ib;font-size: 0.36rem; box-shadow: 0 0.1rem 0.1rem rgba(0,0,0,0.2);
    &:hover {color: #fff;}
    &:active {opacity: 0.8;}
    &.full {color: #fff;.radial; border: none;.txt-ver(0.95rem);box-shadow: none;
        &[disabled]{background: #ccc;}
    }
    &.border {background: none; border: 1px solid @pink; color: @pink;}
}
.mask {.pos(f); left: 0; top:0; right:0; bottom:0; z-index: 9; .d-n;background: rgba(0,0,0,0.7);}
.float {.pos(f); left: 0; top:0; right:0; bottom:0; z-index: 10; .d-n;
    .close {.wh(0.8rem); background: url(../img/ps1/u767.png) no-repeat center center/cover; .d-b;.pos(f); bottom:1.5rem; left: 50%; margin-left: -0.4rem;}
    .inner { width: 6rem; background: #fff; transform: translate(-50%,-50%); .pos(a); left: 50%; top:50%; border-radius: 0.2rem;.tc; box-sizing: border-box;overflow:hidden;
        .txt {padding: 0.5rem; max-height:7rem; overflow: auto;
            h3 {.tc; color: @pink; margin-bottom: 0.3rem;font-size: 0.36rem;}
            p {line-height: 1.5;}
            .pink {color: @pink; .font-wn;}
            input {width: 100%; border: 1px solid #ccc; .txt-ver(0.8rem); .tc; box-shadow: 0 0 20px #eee inset; margin-top: 0.2rem;}
        }
        .btn {width: 100%;border-radius:0 !important;}
        &.qr-code { background: none;
            img {.wh(3.5rem)}
            p {.tc; color: #fff; line-height: 2; margin-top: 0.3em;}
        }
    }
    .share {background: none;color: #fff;.tr;
        img {width:4.92rem; height: auto; margin-right: 0.3rem;margin-top: 0.3rem;}
        p{.tc;font-size: 0.36rem; margin-top: 0.3rem;}
    }
}
.plus {
    @wh:1.12rem;
    .wh(@wh); border: 1px solid #ddd; background: url(../img/ps1/plus.png) no-repeat center center /1rem 1rem;.d-ib; border-radius: 50%; box-sizing: border-box;overflow: hidden;
    &:active {opacity: 0.8;}
    img {.wh(@wh);}
    &.tx {}
}
.list-msg {
    li {padding: 0.05rem 0;}
}
.msg-item {background: rgba(0,0,0,0.5); border-radius: 0.35rem; color: #fff; font-size: 0.24rem; .d-ib; padding-right: 0.3rem;
    img {.wh(0.6rem);border-radius: 50%;margin: 0.05rem 0.1rem 0.05rem 0.06rem;}
}
.time-item{ color: @pink; .tc; padding: 0.3rem 0;
    strong {margin-left: 0.1rem; .linear;color:#fff;line-height:0.40rem;.d-ib;.font-wn;font-family:Arial;padding:0.1rem;margin-right:0.1rem;border-radius:5px;box-shadow:1px 1px 3px rgba(0,0,0,0.2);}
}


.banner {.pos(r);overflow: hidden;
    .img {width: 100%; height: auto;}
    .list-msg {.pos(a); left: 5%; bottom:5%;}
    .logo { width: 3.5rem; height: auto;.pos(a); left: 0.3rem; top:0.3rem;}
    .enter {color: #fae6a1; .tc; font-size: 0.36rem;.font-wb;.pos(a); left: 0; right:0; bottom:0.25rem; text-shadow: 0 0.05rem 0.05rem rgba(0,0,0,0.3);
        b {.d-ib;background: #fae6a1; color: #e91e4d; padding: 0.05rem 0.2rem; border-radius: 0.1rem; margin: 0 0.2rem;text-shadow: none;}
    }
}
.box {background: #f7f7f7; border: 1px solid #e5e5e5; border-radius: 0.15rem; padding: 0.35rem; margin-left:0.22rem;margin-right:0.22rem;
    &.border{margin: 0; border-radius: 0;}
}
.product{
    img { width: 2.7rem; height: 1.6rem; }
    h3 {text-align: justify; .font-wn; line-height: 1.5;margin-left: 0.2rem;}
}

.list-joins{
    li {.pos(r);
        &:after{height: 1px; background: #ccc; .pos(a); left: 0; right:0; width: 100%; content: ""; transform: scaleY(0.33);}
        .plus{margin: 0;}
        u{.pos(r); right:auto;top:auto;background: url(../img/ps1/arr-right.png) no-repeat center center/cover;width: 0.29rem;height: 0.5rem;.d-b; }
    }
}
.list-join{.tc; padding: 0.3rem;.pos(r);
    span {.d-ib;}
    .plus {margin: 0 0.3rem;}
    s {.d-b; overflow: hidden; font-size: 0.22rem;margin-top: 0.1rem; vertical-align: middle;
        .name1 {color: @pink;.d-ib;.txt-ver(0.3rem);vertical-align: middle;}
        .name2 {color: #333;overflow: hidden;text-overflow:ellipsis;white-space: nowrap;.d-ib;max-width: 5em;.txt-ver(0.3rem); vertical-align: middle;}
    }
    u {.pos(a); right:0.3rem; top:0; background: url(../img/ps1/u359.png) no-repeat center center/cover; .wh(0.8rem);.d-n;}
}
.list-join-item{.pos(r);
    h3 {.pos(r);padding-bottom: 0.3rem;.font-wn; margin-bottom: 0.3rem;
        &:after {.pos(a); height: 1px; background: #ccc; left: 0; right:0; bottom: 0; transform: scaleY(0.33); content: '';}
    }
    .list-join {padding: 0;}
    em {.wh(0.8rem); background:#000;
        &.success {background: url(../img/ps1/u359.png) no-repeat center center/cover;}
        &.fail {background: url(../img/ps1/u376.png) no-repeat center center/cover}
    }
    u{.pos(r); right:auto;top:auto;background: url(../img/ps1/arr-right.png) no-repeat center center/cover;width: 0.29rem;height: 0.5rem;.d-b; }
}
.btn-group { .tc;;
    p { padding: 0.2rem 0;width: 3.5rem;.bc;
        a{.d-ib; .tc;text-decoration: underline;
            &:active {color: @pink;}
        }
    }
    .ico-code { background: url(../img/ps1/u103.png) no-repeat left center/0.3rem; padding-left: 0.4rem;
        b {text-decoration: underline;}
    }
    .qr-code { .d-n;
        img {.wh(2.5rem); .bc;}
    }
    .team-bar { width: 5rem;color: @pink;
        b {.font-wn;font-family: arial;color: #666;}
    }
}
.edit-body {
    h3 {color: @pink;}
    ol{ margin-left: 0.3rem; margin-top: 0.2rem;
        li {list-style:decimal; line-height: 1.8; color: #666;text-align: justify; padding: 0.1rem 0; font-size: 0.24rem;}
    }
}
.btn-group-bot {padding: 0.3rem;
    .btn {width: 3.2rem;}
}
.fix-bot-btns {.pos(f); left: 0; right:0; bottom:0;
    a {color: #fff;.tc; .linear; border-left:1px solid #fff; border-right:1px solid #fff;.txt-ver(0.95rem);
        &:active {.radial;}
    }
    a:nth-of-type(1){border-left: none;}    
    a:nth-last-of-type(1){border-right: none;}    
}
.search-top {height: 2.9rem; overflow: hidden;.pos(r);
    b {.pos(a); .wh(20rem); background: @pink; border-radius: 50%;.pos(a); left: 50%; bottom: 0; margin-left: -10rem; z-index: 0;}
    .rule-note {.pos(a); color: #fff; text-decoration: underline; right:0.3rem; top:0.3rem;}
    p {.pos(a); left: 0; right:0; bottom:0.3rem;.tc;
        .tx {.wh(1.15rem);.d-b;.bc;border-radius: 50%;overflow: hidden;
            img {.wh(1.15rem)}
        }
        .name {.d-b; margin-top: 0.2rem;color: #fff;}
    }
}
.foot-logo {.d-b;.bc;width: 4rem;}
.list-jiang {margin: 1rem 0.85rem 1.2rem 0.85rem;
    li {border-bottom: 1px dotted @pink;.txt-ver(0.7rem);
        span:nth-of-type(1) {color: #333;
            b{color: @pink;.font-wn;}
        }
        span:nth-of-type(2){color: #666;.font-wn;
            a {color: @pink;
                b {text-decoration: underline; margin-left: 0.3rem;.font-wn;}
            }
        }
    }
}

.count-code {.pos(a); right:0; top:0; z-index: -1;}



















































